import DfStore from "@/store/DfStore";
import { defineComponent, PropType, Ref } from "vue";
import * as cfModel from "@/models/CusFuns";

type optType = {
    value: string;
    text: string;
};

export default defineComponent({
    name: "CustomizeItem",
    props: {
        typeMessage: {
            type: Object as PropType<cfModel.Content>,
            required: true,
        },

        value: {
            required: true,
        },
    },
    // emits:['update-value'],
    data: () => ({
        inputsMap: new Map(),
    }),

    render() {
        const createOptsFromSource = (
            source: unknown,
            createFunc: (opt: optType) => any
        ) => {
            const opts = (source as optType[]).map((v) => {
                return createFunc(v);
            });

            return opts;
        };

        if (this.$props.typeMessage.type === "input") {
            const onChange = (event: any) => {
                this.$emit("update:value", event.target.value);
            };

            return (
                <a-input
                    onChange={onChange}
                    v-model={[this.$props.value, ["value"]]}
                ></a-input>
            );
        }

        if (this.$props.typeMessage.type === "select") {
            let opts = null;

            if (this.$props.typeMessage.source === "data_columns") {
                const colsInfos = DfStore.getStateReadonly().columnInfo.map(
                    (v) => {
                        return { value: v.field, text: v.label };
                    }
                );

                opts = createOptsFromSource(colsInfos, (v) => (
                    <a-select-option value={v.value}>{v.text}</a-select-option>
                ));
            } else {
                opts = createOptsFromSource(
                    this.$props.typeMessage.source,
                    (v) => (
                        <a-select-option value={v.value}>
                            {v.text}
                        </a-select-option>
                    )
                );
            }

            const onChange = (value: any, option: any) => {
                this.$emit("update:value", value);
            };

            return (
                <a-select
                    onChange={onChange}
                    v-model={[this.$props.value, ["value"]]}
                >
                    {opts}
                </a-select>
            );
        }

        if (this.$props.typeMessage.type === "date-select") {
            const onChange = (time: string, timeString: string) => {
                this.$emit("update:value", timeString);
            };

            return (
                <a-date-picker
                    v-model={[this.$props.value, ["value"]]}
                    show-time
                    type="date"
                    onChange={onChange}
                />
            );
        }

        if (this.$props.typeMessage.type === "checkbox") {
            const onChange = (event: any) => {
                this.$emit("update:value", event);
            };

            const opts = createOptsFromSource(
                this.$props.typeMessage.source,
                (v) => (
                    <a-checkbox value={v.value} name="type">
                        {v.text}
                    </a-checkbox>
                )
            );

            return (
                <a-checkbox-group
                    v-model={[this.$props.value, ["value"]]}
                    onChange={onChange}
                >
                    {opts}
                </a-checkbox-group>
            );
        }

        if (this.$props.typeMessage.type === "switch") {
            const onChange = (checked: Boolean, event: Event) => {
                this.$emit("update:value", checked);
            };

            return (
                <a-switch
                    v-model={[this.$props.value, ["checked"]]}
                    defaultChecked={this.$props.value}
                    onChange={onChange}
                />
            );
        }

        if (this.$props.typeMessage.type === "radio") {
            const onChange = (event: any) => {
                this.$emit("update:value", event.target.value);
            };

            const opts = createOptsFromSource(
                this.$props.typeMessage.source,
                (v) => (
                    <a-radio value={v.value} name="type">
                        {v.text}
                    </a-radio>
                )
            );

            return (
                <a-radio-group
                    defaultValue={this.$props.value}
                    v-model={[this.$props.value, ["value"]]}
                    onChange={onChange}
                >
                    {opts}
                </a-radio-group>
            );
        }
    },
});
