<template>
  <a-form :wrapperCol="{ span: 14 }">
    <a-form-item
      v-for="c in contents"
      :label="c.content.title"
      v-bind:key="c.content.title"
    >
      <!-- <a-input v-model:value="c.input"></a-input> -->
      <CustomizeItem
        :typeMessage="c.content"
        v-model:value="c.input"
      ></CustomizeItem>
    </a-form-item>

    <a-form-item>
      <a-Button @click="onApply">确定</a-Button>
    </a-form-item>
  </a-form>
</template>

<script lang='ts'>
import { defineComponent, markRaw, PropType, reactive, Ref, ref } from "vue";
import CustomizeItem from "./CustomizeItem";
// import * as cfModel from "@/models/CustomizeFunctionModel";
import * as cfModel from "@/models/CusFuns";
import * as apps from "@/applications";

export default defineComponent({
  name: "CustomizeFunction",
  components: { CustomizeItem },
  props: {
    data: {
      type: Object as PropType<cfModel.CusFunUIModel>,
      required: true,
    },
  },

  setup(props) {
    const contents = reactive(
      props.data.contents.map((v) => {
        return {
          content: markRaw(v),
          input: ref(v.defaultValue),
        };
      })
    );

    const onApply = async () => {
      const data: cfModel.CusFunInputRet = {
        uid: props.data.uid,
        contents: contents,
      };

      await apps.uploadCusFunction(data);
    };

    return {
      contents,
      onApply,
    };
  },
});
</script>

