
import { defineComponent, isReactive, isRef, markRaw, ref, toRef } from "vue";
import FileUpload from "./FileUpload/index.vue";
import Query from "./Query/index.vue";
import Groupby from "./Groupby/index.vue";
import CodeBuilder from "./CodeBuilder/index.vue";
import DfStore from "@/store/DfStore";
import CustomizeFunction from "@/components/Commons/CustomizeFunction.vue";
import FunsPool from "@/components/FunctionTabs/FunsPool/index.vue";

export default defineComponent({
  name: "FunctionTabs",
  props: {
    // isDataLoaded: { type: Boolean, required: true },
  },
  components: {
    FileUpload,
    Query,
    Groupby,
    CodeBuilder,
    CustomizeFunction,
    FunsPool,
  },

  setup() {
    let isDataLoaded = DfStore.getStateToRefs().isLoaded;
    let isDev = process.env.NODE_ENV == "development";

    let data = DfStore.getData();
    let columns = DfStore.getColumns();

    let onFilter = ({ filterList }: any) => {
      console.log("onFilter", filterList);
    };

    return {
      isDataLoaded,
      isDev,
      onFilter,
      columns,
      data,
    };
  },
});
