
import { defineComponent, reactive, ref } from "vue";
import CustomizeFunction from "@/components/Commons/CustomizeFunction.vue";
import * as apps from "@/applications";
import { CusFunUIModel } from "@/models/CusFuns";

export default defineComponent({
  name: "FunsPool",
  components: { CustomizeFunction },

  async setup() {
    const show_func = ref(false);
    const uiModel = ref({} as CusFunUIModel);

    const onOpenUI = async (uid: string) => {
      const model = await apps.getCusFunUIModel(uid);
      uiModel.value = model;

      show_func.value = true;
    };

    const cusfuncsDesc = await apps.getCusFunctionsDesc();

    return {
      show_func,
      cusfuncsDesc,
      onOpenUI,
      uiModel,
    };
  },
});
