<template>
  <div class="container">
    <a-button
      v-for="f in cusfuncsDesc"
      :key="f.function_name"
      @click="onOpenUI(f.uid)"
      >{{ f.function_name }}</a-button
    >

    <a-drawer
      width="500"
      :destroyOnClose="true"
      :title="uiModel.function_name"
      placement="right"
      :closable="true"
      v-model:visible="show_func"
    >
      <CustomizeFunction :data="uiModel"></CustomizeFunction>
    </a-drawer>
  </div>
</template>

<script lang='ts'>
import { defineComponent, reactive, ref } from "vue";
import CustomizeFunction from "@/components/Commons/CustomizeFunction.vue";
import * as apps from "@/applications";
import { CusFunUIModel } from "@/models/CusFuns";

export default defineComponent({
  name: "FunsPool",
  components: { CustomizeFunction },

  async setup() {
    const show_func = ref(false);
    const uiModel = ref({} as CusFunUIModel);

    const onOpenUI = async (uid: string) => {
      const model = await apps.getCusFunUIModel(uid);
      uiModel.value = model;

      show_func.value = true;
    };

    const cusfuncsDesc = await apps.getCusFunctionsDesc();

    return {
      show_func,
      cusfuncsDesc,
      onOpenUI,
      uiModel,
    };
  },
});
</script>


<style lang="less" scoped>
div.container {
  text-align: center;
}

.input {
  width: 50vw;
}
</style>

