
import CommandStore from "@/store/CommandStore";
import { computed, defineComponent, ref } from "vue";
import { CloseOutlined, DeleteOutlined } from "@ant-design/icons-vue";
import * as apps from "@/applications";

export default defineComponent({
  name: "Commander",
  components: { CloseOutlined, DeleteOutlined },

  setup() {
    let cmds = CommandStore.getStateToRefs().cmds;
    let cmd_modal = ref(null);

    let status = computed(() => {});

    let onCloseTag = async (id: string) => {
      await apps.removeCmd(id);
    };

    return { cmds, onCloseTag, cmd_modal };
  },
});
